.topbar-container {
    position: sticky;
    top: 0;
    background-color: $mainColor1;
    z-index: 100;
    border-bottom: $outline1;
    width: 100%;
    z-index: 100;
    h1 {
        padding-left: 10px;
        padding-top: 10px;
    }
    .topbar-btns {
        display: flex;
        button {
            color: $mainColor2;
            width: 100%;
            padding: 20px;
            background-color: $mainColor1;
            cursor: pointer;
            border: none;
            outline: none;
            font-family: $mainFont;

            &:hover {
                background-color: $hoverClr2;
            }
            font-size: 16px;
        }
        .selected {
            box-shadow: 0px 5px 0px 0px $logoColor;
            font-weight: 700;
            color: white;
        }
    }
}

.light {
    .topbar-container {
        background-color: $mainColor0;
        color: $mainColor1;
        .topbar-btns {
            button {
                color: $mainColor1;
                width: 100%;
                padding: 20px;
                background-color: $mainColor0;
                cursor: pointer;
                border: none;
                outline: none;
                &:hover {
                    background-color: #e9e9e9;
                }
                font-size: 16px;
            }
            background-color: $mainColor0;
        }
    }
}

@media (max-width: 768px) {
    .topbar-container {
        h1 {
            display: none;
        }
        .topbar-btns {
            button {
                padding: 15px;
            }
        }
    }
}
